import React from "react";
import { Day } from "../Day/Day";
import "./board.scss";
import {
  useCurrentMonthYear,
  WeekDays,
} from "../../ContextCalendar/CalendarContext";
import { Popup } from "../Popup/Popup";
import { IPopup } from "../../allInterfaces";

const daysInMonth = (y: number, m: number): number =>
  new Date(y, m, 0).getDate();

const getDayWeek = (y: number, m: number, d: number): number =>
  new Date(y, m, d).getDay();

const createMonth = (n: number, m: number, y: number): Array<Date> => {
  let firstDay: number = getDayWeek(y, m, 1);
  let listD: Date[] = [];
  for (let cell in Array.from(Array(n).keys())) {
    listD.push(new Date(y, m, Number(cell) - firstDay + 1));
  }
  return listD;
};
const isSixWeek = (m: number, y: number): boolean =>
  getDayWeek(y, m, 1) + daysInMonth(y, m) > 35 ? true : false;

export const Board: React.FC<{ toogleModal: (m: number) => void }> = ({
  toogleModal,
}) => {
  const {
    currentMonthYear: [curMonth, curYear],
  } = useCurrentMonthYear();
  const [currentData, setCurrentData] = React.useState<IPopup>({
    curDay: "",
    x: 0,
    y: 0,
  });
  const [days, setDays] = React.useState<Date[]>([]);
  React.useEffect(() => {
    let colWeek: number = isSixWeek(curMonth, curYear) ? 42 : 35;
    setDays(createMonth(colWeek, curMonth, curYear));
  }, [curMonth]);
  // * Change columns amount
  const sizeBoard: string = `m-board ${
    isSixWeek(curMonth, curYear) ? "large" : ""
  }`;

  const getPopUp = (data: IPopup): void => {
    setCurrentData(data);
  };

  return (
    <div className={sizeBoard}>
      {Object.keys(WeekDays)
        .filter((x) => !(parseInt(x) >= 0))
        .map((_, i) => (
          <div className="m-board__weekdays" key={i}>
            {WeekDays[i]}
          </div>
        ))}
      {days.map((day_, ind) => (
        <Day
          date={day_}
          key={ind}
          toogleModal={toogleModal}
          popupDialog={getPopUp}
        />
      ))}
      {currentData.curDay.length > 0 && <Popup data={currentData} />}
    </div>
  );
};
