import React from "react";
import "./header.scss";
import { Months } from "../Months/Months";
import { HeaderButton } from "../HeaderButton/HeaderButton";
import { useCurrentMonthYear } from "../../ContextCalendar/CalendarContext";

export const Header: React.FC<{ toogleModal: (m: number) => void }> = ({
  toogleModal,
}) => {
  const { changeMonthYear, getGreenStarOnMonth } = useCurrentMonthYear();
  // * Set today
  const setToday = (): void => {
    changeMonthYear(new Date().getMonth(), new Date().getFullYear());
  };
  //  * Show help modal
  const showHelp = (): void => toogleModal(2);

  return (
    <div className="m-header">
      <Months />
      <div className="m-wrapper">
        <HeaderButton
          statusbtn={true}
          text={`${getGreenStarOnMonth()}`}
          image={"g"}
        />
        <HeaderButton statusbtn={true} text={"10"} image={"y"} />
        <HeaderButton
          statusbtn={false}
          text={""}
          image={"z"}
          onclick={showHelp}
        />
        <HeaderButton
          statusbtn={false}
          text={"Today"}
          image={""}
          onclick={setToday}
        />
      </div>
    </div>
  );
};
