import React from "react";
import "./headerbutton.scss";
import logo from "../../assets/image/star_green.png";

interface IButtonHeader {
  statusbtn: boolean;
  image: string;
  text: string;
  onclick?: () => void;
}

export const HeaderButton: React.FC<IButtonHeader> = ({
  statusbtn,
  image,
  text,
  onclick,
}) => {
  const classes: () => string = () => {
    return `m-buttonhdr${statusbtn ? " depressed" : ""}`;
  };

  // const getStar = (ev: React.DragEvent<HTMLDivElement>): void => {
  //   console.log(ev);
  // };

  return (
    <div
      className={classes()}
      onClick={onclick}
      draggable={image === "g" ? true : false}
      // onDragStart={getStar}
    >
      {image && image === "g" && <img src={logo} alt="" />}
      {image && image === "y" && <label className="star">‚≠ê</label>}
      {image && image === "z" && <label className="star">‚ùì</label>}
      <label>{text}</label>
    </div>
  );
};

