import React from "react";
import "./popup.scss";
import {
  useCurrentMonthYear,
  IResault,
} from "../../ContextCalendar/CalendarContext";
import { getMood } from "../../logics/logics";
import { IPopup } from "../../allInterfaces";

export const Popup: React.FC<{ data: IPopup }> = ({ data }) => {
  const { getResaultOnDate } = useCurrentMonthYear();

  let styleCur: { [propName: string]: string | number } = {
    top: data.y,
    left: data.x,
  };

  React.useEffect(() => {
    styleCur = { top: data.y, left: data.x };
  }, [data.curDay]);

  // * Get resaults on this day
  const res: IResault = getResaultOnDate(data.curDay);
  // * Choose emoji
  const [emoji, mood] = getMood(res.greenCounts, res.redCounts);
  if (data.curDay.length < 10) return null;
  return (
    <div className="m-popup" style={styleCur}>
      <div>
        üü¢&nbsp;<span>{res.greenCounts}</span>&nbsp;
        <span className="another">non-natural foods eaten</span>
      </div>
      <div>
        üî¥&nbsp;<span>{res.redCounts}</span>&nbsp;
        <span className="another">natural foods eaten</span>
      </div>
      <div>
        üë£&nbsp;<span>{res.stepCounts}</span>&nbsp;
        <span className="another">steps done</span>
      </div>
      <div>
        {emoji}&nbsp;<span>{mood}</span>&nbsp;
        <span className="another">mood</span>
      </div>
    </div>
  );
};
