import {IHealth} from '../allInterfaces';

export const howGood = (g: number, r: number): IHealth => {
  let res: number = 0
  if (g === 0 && r === 0) return {status: "UNDEFINED"}
  try {
    res = g/r    
  } catch {
    return {status: "UNDEFINED"}
  } 
  if (res > 2)
    return {status: "HAPPY"}
  else if (res <= 1) 
    return {status: "UNHAPPY"}
  else return {status: "NORMAL"}
}

export const getMood = (g: number, r: number): [string, string] => {
  let health = howGood(g, r)
  switch (health.status){
    case "HAPPY":
      return [`üòä`, health.status]
    case "UNHAPPY":
      return [`üôÅ`, health.status]
    case "UNDEFINED":
      return [`üôÅ`, 'UNDEFINED']
    default: 
      return [`üòê`, 'NORMAL']
  }
}