import React from "react";
import "./counter.scss";

interface ICounter {
  value: number;
  changeValue: (n: number) => void;
  minValue?: number;
  maxValue?: number;
  increment?: number;
  showIncrementIcon?: boolean;
}

const setRightValue = (n: number, minV: number, maxV: number): number => {
  if (n >= minV && n <= maxV) return n;
  else if (n < minV) return minV;
  else return maxV;
};

export const Counter: React.FC<ICounter> = ({
  value = 0,
  changeValue,
  minValue = 0,
  maxValue = 100,
  increment = 1,
  showIncrementIcon = true,
}) => {
  // ? Set value used keyboard
  const handleInput = (event: React.ChangeEvent<HTMLInputElement>): void => {
    changeValue(setRightValue(Number(event.target.value), minValue, maxValue));
  };
  // ? Set value used button plus/minus
  const changeCounter = (event: React.MouseEvent): void => {
    changeValue(
      setRightValue(
        event.currentTarget.className === "m-counter__decr"
          ? value - increment
          : value + increment,
        minValue,
        maxValue
      )
    );
  };

  return (
    <div className="m-counter">
      <div className="m-counter__wrapper">
        {showIncrementIcon && (
          <div
            className="m-counter__decr"
            onClick={(event) => changeCounter(event)}
          >
            ‚ûñ
          </div>
        )}
        <input
          className="m-counter__input"
          type="number"
          value={value}
          onChange={(event) => handleInput(event)}
        />
        {showIncrementIcon && (
          <div
            className="m-counter__incr"
            onClick={(event) => changeCounter(event)}
          >
            ‚ûï
          </div>
        )}
      </div>
    </div>
  );
};
